

<template>
  <div class="loginandout">

    <transition name="box">

      <div v-show='showlogins' class="itembox">
          <div class='autosay'>
              <!-- <p>这是一段文字。。</p> -->
              <div class="itemdis">

                  <div class="levalslive" v-for="(item,index) in membornday">
                        <div class="logoimg">

                            <img v-if="item=='本人' && whoareyougetlive=='先生'" src="../../../static/img/dady.png" alt="">
                            <img v-if="item=='本人' && whoareyougetlive=='女士'" src="../../../static/img/mami.png" alt="">

                            <img v-if="item=='配偶' && whoareyougetlive=='先生'" src="../../../static/img/mami.png" alt="">
                            <img v-if="item=='配偶' && whoareyougetlive=='女士'" src="../../../static/img/dady.png" alt="">

                              <!-- <img v-if="item=='本人'" src="../../../static/img/dady.png" alt=""> -->
                              <!-- <img v-else-if="item=='配偶'" src="../../../static/img/mami.png" alt=""> -->


                              <img v-else-if="item=='儿子'" src="../../../static/img/son.png" alt="">
                              <img v-else-if="item=='女儿'" src="../../../static/img/girls.png" alt="">
                              <img v-else-if="item=='小儿子'" src="../../../static/img/son.png" alt="">
                              <img v-else-if="item=='小女儿'" src="../../../static/img/girls.png" alt="">

                              <div>{{item}}</div>
                        </div>
                        <div class="logotext">
                              <div class="inputtext">
                                  <input readOnly='readOnly' @click="getArea(index)" class="ipt" @blur="inputevt(index)"  type="text" :placeholder="citymsg" value="">
                              </div>
                        </div>

                  </div>


              </div>



          </div>

      </div>
    </transition>



  </div>
</template>

<script>
export default {
  data(){
    return{
      // 注意：这是用户刚开始选择的成员
       familymembers:["本人","配偶","儿子","女儿"],
       hhh:"",
       input_indexmsgs:[],
       msgbox:[false,false,false,false,false,false]

    }
  },
  props : {
        showlogins : {
            type : Boolean,
            required : true
        },
         membornday : {
            type : Array,
            required : true
        },
         homeareaitem : {
            type : Array,
            required : true
        },
          whoareyougetlive : {
            type : String,
            required : true
        },
          citymsg : {
            type : String,
            required : true
        }
    },
  methods:{
    // closemoreprojects:function(){
    //   this.$emit('closeprojects');
    // },
    // 
    // 失去焦点事件
    inputevt:function(index){
      var that = this;
      setTimeout(function(){

        // memborndaylive是一个传值
        // 根据不同失去焦点，进行赋值
        if(index==0){
            $(".levalslive:eq(0)").find("input").val(that.homeareaitem[0]);

            // 设置其它的input里面的输入答案是一样的
            for(var h = 0;h<that.membornday.length;h++){
                  $(".levalslive:eq("+h+")").find("input").val(that.homeareaitem[0]);
            }
  
        }
        else if(index==1){
            $(".levalslive:eq(1)").find("input").val(that.homeareaitem[1]);
        }
        else if(index==2){
            $(".levalslive:eq(2)").find("input").val(that.homeareaitem[2]);
        }
        else if(index==3){
            $(".levalslive:eq(3)").find("input").val(that.homeareaitem[3]);
        }
        else if(index==4){
            $(".levalslive:eq(4)").find("input").val(that.homeareaitem[4]);
        }
        else if(index==5){
            $(".levalslive:eq(5)").find("input").val(that.homeareaitem[5]);
        }

    
        // 获取input里面遍历的地区值，发送到appvue组件
        for(var j = 0;j<that.homeareaitem.length;j++){
            if(j==index){
                that.input_indexmsgs[index]=$(".levalslive:eq("+index+")").find("input").val()
                // console.info($(".levalslive:eq("+index+")").find("input").val());
                that.$emit('inputmsglive',that.input_indexmsgs);
                // that.$emit('inputmsglive',"abc");
                
            }

        }


  
   },160)



        
    },



    //打开日期选择器
    getArea:function(index){


         console.info("拿到了日期："+this.memborndayitem)

         // 点击第一个input，就发送一个时间过去
          
          var that =this;
          this.msgbox=[false,false,false,false,false,false];


          if(index==0){
             console.info("打开了0开关------")
              // msgboxfunc(0)
              
               for(var i =0;i<this.msgbox.length;i++){
                that.msgbox[i]=false;
              }
               that.msgbox[0]=true;
          }
          else if(index==1){
             console.info("打开了1开关----")
                  // msgboxfunc(1)
          for(var i =0;i<this.msgbox.length;i++){
                that.msgbox[i]=false;
                  }
               that.msgbox[1]=true;



          }
           else if(index==2){
              console.info("打开了2开关----")
                   // msgboxfunc(2)
              for(var i =0;i<this.msgbox.length;i++){
                that.msgbox[i]=false;
                  }
               that.msgbox[2]=true;
          }
           else if(index==3){
              console.info("打开了3开关----")
                   // msgboxfunc(3)
              for(var i =0;i<this.msgbox.length;i++){
                that.msgbox[i]=false;
                  }
               that.msgbox[3]=true;
          }
           else if(index==4){
              console.info("打开了4开关----")
                   // msgboxfunc(4)
                    for(var i =0;i<this.msgbox.length;i++){
                that.msgbox[i]=false;
                  }
               that.msgbox[4]=true;
          }
           else if(index==5){
              console.info("打开了5开关----")
                  // msgboxfunc(5)
               for(var i =0;i<this.msgbox.length;i++){
                that.msgbox[i]=false;
                  }
               that.msgbox[5]=true;
          }

          console.info(that.msgbox+"纽约")

            // 发送开关信息
        this.$emit('opendatelive',this.msgbox);


        // 点击设置模块显示为true
        var openareamodule = true;
        this.$emit('inputmsglive',openareamodule);
          
    },
 
    components:{
            props:{
              // informationget:String
            }
        }

  }
  // created:function(){

  //     this.citymsgs = this.citymsg;
  //     console.info("111")
  //   }



}
</script>

<style scoped>

ul,li,p,div,section,header,nav,span,ol,dl,tr,td,th,em,i{
  padding:0;
  margin:0;
}

.box-enter-active,.box-leave-active{
        /*transition: all 1s;*/
    }
.box-enter,.box-leave-active {
        /*transform: translateX(-100%);
        opacity: 0;*/
    }

.itembox{
  width:100%;
  height:auto;

}



.autosay{
  width:100%;
  background:#fff;
  display: inline-block;
  height:auto;
  border-radius: 12px;
  margin-top:10px;
  margin-bottom:5px;


}

.itemdis{
  width:100%;
  height:auto;
}
.levalslive{
  width:80%;
  height:100%;
  margin:0 auto;
  display: flex;
  font-size: 14px;
}

.logoimg{
  width:80px;
  height:100%;

}
.logoimg img{
  width:50px;
  height:50px;
  display: block;
  margin:0 auto;
  vertical-align: top;
  margin-top:6px;
}
.logoimg div{
  font-size:12px;
  text-align: center;
  margin-top:5px;
}

.logotext{
  flex:1;
}

.inputtext{
  width:170px;
  height:26px;
  margin:0 auto;
  margin-top:22px;
}
.inputtext input{
  width:100%;
  height:100%;
  line-height: 100%;
  border:none;
  outline: none;
  border-radius: 16px;
  background:   rgb(255,238,172);;
  box-sizing:border-box;
  text-align: center;
  color:rgb(65,65,65);
  font-size: 12px;
}

input{
  outline:none;
}


input::-webkit-input-placeholder {
       color: rgb(65,65,65);
   }
input::-moz-input-placeholder {
       color: rgb(65,65,65);
   }

input::-ms-input-placeholder {
       color: rgb(65,65,65);
   }







/**{-webkit-tap-highlight-color: rgba(0,0,0,0);-webkit-tap-highlight-color:transparent;}  */



</style>

























