

<template>
  <div class="loginandout">

    <transition name="box">

      <div v-show='showlogins' class="itembox">
          <div class='autosay'>
              <div class="itemdis">

                  <!-- <div class="levals" v-for="(item,index) in familymembers"> -->
                  <div class="levals" v-for="(item,index) in vuefamilymsgitem">
                        <div class="logoimg">


                          <img v-if="item=='本人' && whoareyougetsociety=='先生'" src="../../../static/img/dady.png" alt="">
                            <img v-if="item=='本人' && whoareyougetsociety=='女士'" src="../../../static/img/mami.png" alt="">

                            <img v-if="item=='配偶' && whoareyougetsociety=='先生'" src="../../../static/img/mami.png" alt="">
                            <img v-if="item=='配偶' && whoareyougetsociety=='女士'" src="../../../static/img/dady.png" alt="">

                              <!-- <img v-if="item=='本人'" src="../../../static/img/dady.png" alt=""> -->
                              <!-- <img v-else-if="item=='配偶'" src="../../../static/img/mami.png" alt=""> -->


                              <img v-else-if="item=='儿子'" src="../../../static/img/son.png" alt="">
                              <img v-else-if="item=='女儿'" src="../../../static/img/girls.png" alt="">
                              <img v-else-if="item=='小儿子'" src="../../../static/img/son.png" alt="">
                              <img v-else-if="item=='小女儿'" src="../../../static/img/girls.png" alt="">
                              <div>{{item}}</div>
                        </div>
                        <div class="logotext"  @click="labelclickins(index)">


                              <div class="chooseinsornot f1">
                                    <label>
                                          <input class="inp0" v-if="index==0" type="radio" name="societyinsmsg0">
                                          <input class="inp1" v-if="index==1" type="radio" name="societyinsmsg1">
                                          <input class="inp2" v-if="index==2" type="radio" name="societyinsmsg2">
                                          <input class="inp3" v-if="index==3" type="radio" name="societyinsmsg3">
                                          <input class="inp4" v-if="index==4" type="radio" name="societyinsmsg4">
                                          <input class="inp5" v-if="index==5" type="radio" name="societyinsmsg5">
                                          有社保
                                    </label>
                              </div>

                               <div class="chooseinsornot f2">
                                    <label>
                                          <input v-if="index==0" type="radio" name="societyinsmsg0">
                                          <input v-if="index==1" type="radio" name="societyinsmsg1">
                                          <input v-if="index==2" type="radio" name="societyinsmsg2">
                                          <input v-if="index==3" type="radio" name="societyinsmsg3">
                                          <input v-if="index==4" type="radio" name="societyinsmsg4">
                                          <input v-if="index==5" type="radio" name="societyinsmsg5">
                                          无社保
                                    </label>
                              </div>


                              


                        </div>

                  </div>


              </div>



          </div>

      </div>
    </transition>



  </div>
</template>

<script>
export default {
  data(){
    return{
      // 注意：这是用户刚开始选择的成员
       familymembers:["本人","配偶","儿子","女儿"],
       hhh:"",
       input_indexmsgs:[],
       insurancearr:[]

    }
  },

  
  props : {
        showlogins : {
            type : Boolean,
            required : true
        },
        vuefamilymsgitem : {
            type : Array,
            required : true
        },
         whoareyougetsociety : {
            type : String,
            required : true
        }
    },
  methods:{
    // closemoreprojects:function(){
    //   this.$emit('closeprojects');
    // },
    inputevt:function(index){
    },
    clickinputevt:function(){
    },

    // 点击有社保的情况
    labelclickins:function(index){
        // 根据index来进行判断
        // insurancearr=[有社保，无社保，有社保]；
        
        if(index==0){
              if($(".levals").find(".inp0").prop("checked")){
                    this.insurancearr[0]="有社保"
              }
              else{
                    this.insurancearr[0]="无社保"
              }

        }
        if(index==1){
               if($(".levals").find(".inp1").prop("checked")){
                    this.insurancearr[1]="有社保"
              }
              else{
                    this.insurancearr[1]="无社保"
              }

        }
         if(index==2){
               if($(".levals").find(".inp2").prop("checked")){
                    this.insurancearr[2]="有社保"
              }
              else{
                    this.insurancearr[2]="无社保"
              }

        }
         if(index==3){
               if($(".levals").find(".inp3").prop("checked")){
                    this.insurancearr[3]="有社保"
              }
              else{
                    this.insurancearr[3]="无社保"
              }

        }
         if(index==4){
               if($(".levals").find(".inp4").prop("checked")){
                    this.insurancearr[4]="有社保"
              }
              else{
                    this.insurancearr[4]="无社保"
              }

        }
          if(index==5){
               if($(".levals").find(".inp5").prop("checked")){
                    this.insurancearr[5]="有社保"
              }
              else{
                    this.insurancearr[5]="无社保"
              }

        }

        console.info("所有的社保数据"+this.insurancearr);

        this.$emit('insurancemsg',this.insurancearr);


    },











 


    components:{
            props:{
              // informationget:String
            }
        }






  },
 

  


}
</script>

<style scoped>

ul,li,p,div,section,header,nav,span,ol,dl,tr,td,th,em,i{
  padding:0;
  margin:0;
}

.box-enter-active,.box-leave-active{
        /*transition: all 1s;*/
    }
.box-enter,.box-leave-active {
        /*transform: translateX(-100%);
        opacity: 0;*/
    }

.itembox{
  width:100%;
  height:auto;

}



.autosay{
  width:100%;
  background:#fff;
  display: inline-block;
  height:auto;
  border-radius: 12px;
  margin-top:10px;
  margin-bottom:5px;

}

.itemdis{
  width:100%;
  height:auto;
}
.levals{
  width:80%;
  height:100%;
  margin:0 auto;
  display: flex;
  font-size: 14px;
}

.logoimg{
  width:80px;
  height:100%;

}
.logoimg img{
  width:50px;
  height:50px;
  display: block;
  margin:0 auto;
  vertical-align: top;
  margin-top:6px;
}
.logoimg div{
  font-size:12px;
  text-align: center;
  margin-top:5px;
}

.logotext{
  flex:1;
  padding-top:18px;
}

.chooseinsornot{
  width:50%;
  height:100%;
  float:left;
}
/**{-webkit-tap-highlight-color: rgba(0,0,0,0);-webkit-tap-highlight-color:transparent;}  */

</style>

























