

<template>
  <div class="loginandout">

    <transition name="box">

      <div v-show='showlogins' class="itembox">
          <div class='autosay'>
              <!-- <p>这是一段文字。。</p> -->
              <div class="itemdis">


                  <!-- vueincomemsg 是一个所有成员的年龄数组, -->
                  <div v-show="vueincomemsgagebox[index]>=18" class="levalsbox" v-for="(item,index) in vueincomemsg">

                    
                        <div class="logoimg">
                           <img v-if="item=='本人' && whoareyougetincome=='先生'" src="../../../static/img/dady.png" alt="">
                            <img v-if="item=='本人' && whoareyougetincome=='女士'" src="../../../static/img/mami.png" alt="">

                            <img v-if="item=='配偶' && whoareyougetincome=='先生'" src="../../../static/img/mami.png" alt="">
                            <img v-if="item=='配偶' && whoareyougetincome=='女士'" src="../../../static/img/dady.png" alt="">

                              <!-- <img v-if="item=='本人'" src="../../../static/img/dady.png" alt=""> -->
                              <!-- <img v-else-if="item=='配偶'" src="../../../static/img/mami.png" alt=""> -->


                              <img v-else-if="item=='儿子'" src="../../../static/img/son.png" alt="">
                              <img v-else-if="item=='女儿'" src="../../../static/img/girls.png" alt="">
                              <img v-else-if="item=='小儿子'" src="../../../static/img/son.png" alt="">
                              <img v-else-if="item=='小女儿'" src="../../../static/img/girls.png" alt="">
                              <div>{{item}}</div>
                        </div>
                        <div class="logotext">
                              <div class="inputtext">
                                  <input type="number" class="ipt" @blur="inputevt(index)" placeholder="输入家庭收入">万元
                              </div>
                        </div>

                      



                  </div>


                    <!-- 贷款 -->
                   <div class="levalsbox_load">
                        <div class="logoimg">
               
                              <img src="../../../static/img/loan.png" alt="">
                          
                              <div>家庭贷款</div>
                        </div>
                        <div class="logotext">
                              <div class="inputtext getLoanval" >
                                  <input type="number" class="ipt" @blur="getLoan" placeholder="输入家庭贷款">万元
                              </div>
                        </div>

                  </div>








              </div>



          </div>

      </div>
    </transition>



  </div>
</template>

<script>
export default {
  data(){
    return{
      // 注意：这是用户刚开始选择的成员
       familymembers:["本人","配偶","儿子","女儿"],
       hhh:"",
       input_indexmsgs:[],
       vueincomemsgagebox_computed:[],
       loaninputmsg:0 //默认贷款金额填写是0

    }
  },
  props : {
        showlogins : {
            type : Boolean,
            required : true
        },
        vueincomemsg : {
            type : Array,
            required : true
        },
          vueincomemsgagebox : {
            type : Array,
            required : true
        },
        whoareyougetincome : {
            type : String,
            required : true
        }
    },

  // watch:{  
  //       vueincomemsgagebox:{  
  //               handler:function(val,oldval){  
  //                   console.log("观察数组内容改变:"+val.name)  
  //               },  
  //               deep:true//对象内部的属性监听，也叫深度监听  
  //       }

  //  },




  methods:{
    // closemoreprojects:function(){
    //   this.$emit('closeprojects');
    // },
    inputevt:function(index){
        console.info("thinpad 所有家庭成员"+this.vueincomemsg)
        console.info("获得家庭成员年龄---数组"+this.vueincomemsgagebox)
        console.info("战场高级语言:index:"+index);//0,1,3
        for(var j = 0;j<this.vueincomemsg.length;j++){
            if(j==index){
                  console.info("战场高级语言222:index:"+index);//0,1,3

                this.input_indexmsgs[index]=$(".levalsbox:eq("+index+")").find("input").val()
                console.info("找$判断index input里面的值"+$(".levalsbox:eq("+index+")").find("input").val());

                  console.info("找$判断222index input里面的值"+$(".levalsbox:eq("+2+")").find("input").val());




                this.$emit('forincomemsg',this.input_indexmsgs);
                   console.info("战 狼 2--内部--获得input_indexmsgs数组"+this.input_indexmsgs)
  
            }

        }

        // 注意：这个地方index有问题

        console.info("战狼2获得input_indexmsgs数组0"+this.input_indexmsgs[0]) //有值
        console.info("战狼2获得input_indexmsgs数组1"+this.input_indexmsgs[1])//有值
        console.info("战狼2获得input_indexmsgs数组2"+this.input_indexmsgs[2])//有值
        console.info("战狼2获得input_indexmsgs数组3"+this.input_indexmsgs[3])// undefined

















      

    },



    getLoan:function(){
                // 获取贷款
                this.loaninputmsg=$(".getLoanval").find("input").val()
                this.$emit('loaninputmsgdata',this.loaninputmsg);


    },

    clickinputevt1:function(){

    },

 


    components:{
            props:{
              // informationget:String
            }
        }






  },
 

  


}
</script>

<style scoped>

ul,li,p,div,section,header,nav,span,ol,dl,tr,td,th,em,i{
  padding:0;
  margin:0;
}

.box-enter-active,.box-leave-active{
        /*transition: all 1s;*/
    }
.box-enter,.box-leave-active {
        /*transform: translateX(-100%);
        opacity: 0;*/
    }

.itembox{
  width:100%;
  height:auto;

}



.autosay{
  width:100%;
  background:#fff;
  display: inline-block;
  height:auto;
  border-radius: 12px;
  margin-top:10px;
  margin-bottom:5px;

}

.itemdis{
  width:100%;
  height:auto;
}
.levalsbox{
  width:80%;
  height:100%;
  margin:0 auto;
  display: flex;
  font-size: 14px;
}
.levalsbox_load{
    width:80%;
  height:100%;
  margin:0 auto;
  display: flex;
  font-size: 14px;
}

.logoimg{
  width:80px;
  height:100%;

}
.logoimg img{
  width:50px;
  height:50px;
  display: block;
  margin:0 auto;
  vertical-align: top;
  margin-top:6px;
}
.logoimg div{
  font-size:12px;
  text-align: center;
  margin-top:5px;
}

.logotext{
  flex:1;
}

.inputtext{
  width:150px;
  height:26px;
  line-height: 26px;
  margin:0 auto;
  margin-top:22px;
}
.inputtext input{
  width:65%;
  height:100%;
  border:none;
  outline: none;
  border-radius: 16px;
  background:   rgb(255,238,172);;
  box-sizing:border-box;
  text-align: center;
  margin-right:4px;


}


*{-webkit-tap-highlight-color: rgba(0,0,0,0);-webkit-tap-highlight-color:transparent;}  



</style>

























