

<template>
  <div class="loginandout">

        <!-- 说明：slideInLeft之类动画起作用。不过如果styel里定义了样式，那么就以style动画样式为主 -->
 <!--    <transition name="box" enter-active-class="animated slideInLeft" leave-active-class="animated rotateOut">
      <div v-show='showlogins'>
       		<p>12333333333333</p>
      </div>
    </transition> -->


    <transition name="box" enter-active-class="animated slideInLeft" leave-active-class="animated rotateOut">

      <div v-show='showlogins' class="itembox">
          <div class='autosay'>
                <div>
                     <slot name="f1"></slot>
                </div>
                 <div>
                     <slot name="f2"></slot>
                </div>
                 <div>
                     <slot name="f3"></slot>
                </div>
                 <div>
                     <slot name="f4"></slot>
                </div>

          </div>

      </div>
    </transition>



  </div>
</template>

<script>
export default {
  data(){
    return{
      // data:
    }
  },
  props : {
        showlogins : {
            type : Boolean,
            required : true
        }
    },
  methods:{
    // closemoreprojects:function(){
    //   this.$emit('closeprojects');
    // },
  }

}
</script>

<style scoped>

ul,li,p,div,section,header,nav,span,ol,dl,tr,td,th,em,i{
  padding:0;
  margin:0;
}

.box-enter-active,.box-leave-active{
        /*transition: all 1s;*/
    }
.box-enter,.box-leave-active {
        /*transform: translateX(-100%);
        opacity: 0;*/
    }

.itembox{
  width:100%;
  height:auto;
  /*background: #fff;*/
}

/**{-webkit-tap-highlight-color: rgba(0,0,0,0);-webkit-tap-highlight-color:transparent;}  */

.autosay{
  width:auto;
  min-width: 60px;
  max-width: 85%;
  /*background:#ff5983;*/
  background:#fff;
  margin-left:18px;
  display: inline-block;

  height:auto;
  border-radius: 12px;

  margin-top:16px;
  margin-bottom:16px;
   padding:8px;
   padding-left:15px;

}


.autosay div{
  /*display: inline-block;*/
  margin-top:3px;
  margin-bottom:2px;
}















</style>

























