

<template>
  <div class="loginandout">

    <transition name="box">

      <div v-show='showlogins' class="itembox">
          <div class='autosay'>
              <!-- <p>这是一段文字。。</p> -->
              <div class="itemdis">

                  <div class="levals" v-for="(item,index) in membornday">
                        <div class="logoimg">
                                <img v-if="item=='本人' && whoareyougetborn=='先生'" src="../../../static/img/dady.png" alt="">
                                <img v-if="item=='本人' && whoareyougetborn=='女士'" src="../../../static/img/mami.png" alt="">

                                <img v-if="item=='配偶' && whoareyougetborn=='先生'" src="../../../static/img/mami.png" alt="">
                                <img v-if="item=='配偶' && whoareyougetborn=='女士'" src="../../../static/img/dady.png" alt="">

                              <!-- <img v-if="item=='本人'" src="../../../static/img/dady.png" alt=""> -->
                              <!-- <img v-else-if="item=='配偶'" src="../../../static/img/mami.png" alt=""> -->


                              <img v-else-if="item=='儿子'" src="../../../static/img/son.png" alt="">
                              <img v-else-if="item=='女儿'" src="../../../static/img/girls.png" alt="">
                              <img v-else-if="item=='小儿子'" src="../../../static/img/son.png" alt="">
                              <img v-else-if="item=='小女儿'" src="../../../static/img/girls.png" alt="">

                              <div>{{item}}</div>
                        </div>
                        <div class="logotext">
                              <div class="inputtext">
                                  <input readOnly='readOnly' @click="clickinputevt(index)" class="ipt" @blur="inputevt(index)"  type="text" placeholder="请输入出生日期" value="">
                              </div>
                        </div>

                  </div>


              </div>



          </div>

      </div>
    </transition>



  </div>
</template>

<script>
export default {
  data(){
    return{
      // 注意：这是用户刚开始选择的成员
       familymembers:["本人","配偶","儿子","女儿"],
       hhh:"",
       input_indexmsgs:[],
       msgbox:[false,false,false,false,false,false]

    }
  },
  props : {
        showlogins : {
            type : Boolean,
            required : true
        },
         membornday : {
            type : Array,
            required : true
        },
         memborndayitem : {
            type : Array,
            required : true
        },
          whoareyougetborn : {
            type : String,
            required : true
        }
    },
  methods:{
    // closemoreprojects:function(){
    //   this.$emit('closeprojects');
    // },
    // 
    // 失去焦点事件
    inputevt:function(index){
      var that = this;
      setTimeout(function(){
        // 根据不同失去焦点，进行赋值
        if(index==0){
            $(".levals:eq(0)").find("input").val(that.memborndayitem[0]);
        }
        else if(index==1){
            $(".levals:eq(1)").find("input").val(that.memborndayitem[1]);
        }
        else if(index==2){
            $(".levals:eq(2)").find("input").val(that.memborndayitem[2]);
        }
        else if(index==3){
            $(".levals:eq(3)").find("input").val(that.memborndayitem[3]);
        }
        else if(index==4){
            $(".levals:eq(4)").find("input").val(that.memborndayitem[4]);
        }
        else if(index==5){
            $(".levals:eq(5)").find("input").val(that.memborndayitem[5]);
        }


    
        //获取input里面遍历的生日值，发送到appvue组件
        // for(var j = 0;j<that.familymembers.length;j++){
        for(var j = 0;j<that.membornday.length;j++){
            if(j==index){
                that.input_indexmsgs[index]=$(".levals:eq("+index+")").find("input").val()
                console.info($(".levals:eq("+index+")").find("input").val());
                that.$emit('inputmsg',that.input_indexmsgs);

                // 打开一个开关
                // 当聚焦到第几个的时候，把相应的日期确定值的内容，返回。
                

            }

        }


  
   },160)








        
    },

    //打开日期选择器
    clickinputevt:function(index){

          
          var that =this;
          this.msgbox=[false,false,false,false,false,false];


          if(index==0){
            
               for(var i =0;i<this.msgbox.length;i++){
                that.msgbox[i]=false;
              }
               that.msgbox[0]=true;
          }
          else if(index==1){

          for(var i =0;i<this.msgbox.length;i++){
                that.msgbox[i]=false;
                  }
               that.msgbox[1]=true;



          }
           else if(index==2){

              for(var i =0;i<this.msgbox.length;i++){
                that.msgbox[i]=false;
                  }
               that.msgbox[2]=true;
          }
           else if(index==3){
              for(var i =0;i<this.msgbox.length;i++){
                that.msgbox[i]=false;
                  }
               that.msgbox[3]=true;
          }
           else if(index==4){
                    for(var i =0;i<this.msgbox.length;i++){
                that.msgbox[i]=false;
                  }
               that.msgbox[4]=true;
          }
           else if(index==5){

               for(var i =0;i<this.msgbox.length;i++){
                that.msgbox[i]=false;
                  }
               that.msgbox[5]=true;
          }


            // 发送开关信息
        this.$emit('opendate',this.msgbox);


          
    },
 
    components:{
            props:{
              // informationget:String
            }
        }






  },
 

  


}
</script>

<style scoped>

ul,li,p,div,section,header,nav,span,ol,dl,tr,td,th,em,i{
  padding:0;
  margin:0;
}

.box-enter-active,.box-leave-active{
        /*transition: all 1s;*/
    }
.box-enter,.box-leave-active {
        /*transform: translateX(-100%);
        opacity: 0;*/
    }

.itembox{
  width:100%;
  height:auto;

}



.autosay{
  width:100%;
  background:#fff;
  display: inline-block;
  height:auto;
  border-radius: 12px;
  margin-top:10px;
  margin-bottom:5px;

}

.itemdis{
  width:100%;
  height:auto;
}
.levals{
  width:80%;
  height:100%;
  margin:0 auto;
  display: flex;
  font-size: 14px;
}

.logoimg{
  width:80px;
  height:100%;

}
.logoimg img{
  width:50px;
  height:50px;
  display: block;
  margin:0 auto;
  vertical-align: top;
  margin-top:6px;
}
.logoimg div{
  font-size:12px;
  text-align: center;
  margin-top:5px;
}

.logotext{
  flex:1;
}

.inputtext{
  width:120px;
  height:26px;
  margin:0 auto;
  margin-top:18px;
}
.inputtext input{
  width:100%;
  height:100%;
  border:none;
  outline: none;
  border-radius: 16px;
  background:   rgb(255,238,172);;
  box-sizing:border-box;
  color:rgb(65,65,65);
  font-size: 12px;
  text-align: center;
}

input{
  outline:none;
}

/**{-webkit-tap-highlight-color: rgba(0,0,0,0);-webkit-tap-highlight-color:transparent;}  */




</style>

























