

<template>
  <div class="loginandout">



    <transition name="box">

      <div v-show='showlogins' class="itembox">
          <div class='autosay'>
               <div class="fmember">
                    <!-- <p>{{informationget}}</p> -->

                    <!-- <div class="toplogo" @click="choosemember(index,item)" v-for="(item,index) in familymembers"> -->
                    <div class="toplogo" @click="choosemember(index,item)" v-for="(item,index) in familymembers.concat(informationget)">
                          <div class="logoimg">
                           <!--    如果用户点击 先生，则配偶头像默认为女性；
                              点击女士，配偶头像为男士 -->

                              <!-- whoareyouget :先生？女性？ -->
                              
                                <img v-if="item=='本人' && whoareyouget=='先生'" src="../../../static/img/dady.png" alt="">
                                <img v-if="item=='本人' && whoareyouget=='女士'" src="../../../static/img/mami.png" alt="">

                                <img v-if="item=='配偶' && whoareyouget=='先生'" src="../../../static/img/mami.png" alt="">
                                <img v-if="item=='配偶' && whoareyouget=='女士'" src="../../../static/img/dady.png" alt="">


                              <img v-else-if="item=='儿子'" src="../../../static/img/son.png" alt="">
                              <img v-else-if="item=='女儿'" src="../../../static/img/girls.png" alt="">
                              <img class="littleson" v-else-if="item=='小儿子'" src="../../../static/img/son.png" alt="">
                              <img class="littlegirl" v-else-if="item=='小女儿'" src="../../../static/img/girls.png" alt="">
                              
                          </div>

                          <div class="logotext">
                              <p>{{item}}</p>
                          </div>

                          <!-- 这是遮罩 -->
                           <span v-show="false"></span>

  
                    </div>

                  
                    <div class="toplogo">
                          <div class="logoimg addincon">
                              <img @click="addnew" src="../../../static/img/addto.png" alt="">
                          </div>
                          <div class="logotext">
                            <p>添加</p>
                          </div>
                    </div>



                   


               </div>
          </div>

      </div>
    </transition>



  </div>
</template>

<script>
export default {
  data(){
    return{
      // data:
      familymembers:["本人","配偶","儿子","女儿"],
      span_show:[] ,
      // informationget:"这是子组件",
      addfmdatasbox_link:false,
      memitem0:0,
      memitem1:0,
      memitem2:0,
      memitem3:0,
      memitem4:0,
      memitem5:0
    }
  },
  props : {
        showlogins : {
            type : Boolean,
            required : true
        },
        informationget : {
            type : Array,
            required : true
        },
         addfmdatasbox : {
            type : Boolean,
            required : true
        },
        whoareyouget : {
            type : String,
            required : true
        }
    },
  methods:{
    // closemoreprojects:function(){
    //   this.$emit('closeprojects');
    // },
    //选择家庭成员，找值，然后给效果
    choosemember:function(index,item){

        // informationget 判断是否有数据，
      

         // 如果选好了4，5，这里没有点击
         // 点其他的

         // 点2个的。

        // 在关闭模态窗的时候，直接添加到choosedone 的数组里面，如果再次点击，则按照规则来

        if(index<4 && this.memitem4==0 && this.memitem5==0){

                if( this.informationget.indexOf("小儿子")>(-1) && this.informationget.indexOf("小女儿")==(-1)   ){
                      console.info("拿到小儿子")
                     this.span_show[4]=this.familymembers.concat(this.informationget)[4];
                }
                else if(this.informationget.indexOf("小女儿")>(-1) &&  this.informationget.indexOf("小儿子")==(-1) ){
                     console.info("拿到小女儿")
                   this.span_show[4]=this.familymembers.concat(this.informationget)[4];
                }
                else if(this.informationget.indexOf("小女儿")>(-1) && this.informationget.indexOf("小儿子")>(-1) ){
                      console.info("两个都拿到。。")
                       this.span_show[4]=this.familymembers.concat(this.informationget)[4];
                      this.span_show[5]=this.familymembers.concat(this.informationget)[5];

                }
              
        }

        else if( index==4 && this.memitem4==0 ){
             // 取消4的选择
                 if( this.informationget.indexOf("小儿子")>(-1) && this.informationget.indexOf("小女儿")==(-1)   ){
                      console.info("拿到小儿子")
                      this.span_show[4]="";
                      this.memitem4++;
                }
                else if(this.informationget.indexOf("小女儿")>(-1) &&  this.informationget.indexOf("小儿子")==(-1) ){
                     console.info("拿到小女儿");
                   this.memitem4++;
                   this.span_show[4]="";
                }
                else if(this.informationget.indexOf("小女儿")>(-1) && this.informationget.indexOf("小儿子")>(-1) ){
                      console.info("两个都拿到。。")
                      console.info("明月1")
                       this.memitem4++;
                       this.span_show[4]="";
                      this.span_show[5]=this.familymembers.concat(this.informationget)[5];

                }
        
        }
        else if( index==5 && this.memitem5==0){
             // 取消4的选择
               if( this.informationget.indexOf("小儿子")>(-1) && this.informationget.indexOf("小女儿")==(-1)   ){
                      console.info("拿到小儿子")
                      this.span_show[5]="";
                      this.memitem5++;
                }
                else if(this.informationget.indexOf("小女儿")>(-1) &&  this.informationget.indexOf("小儿子")==(-1) ){
                     console.info("拿到小女儿");
                   this.memitem5++;
                   this.span_show[5]="";
                }
                else if(this.informationget.indexOf("小女儿")>(-1) && this.informationget.indexOf("小儿子")>(-1) ){
                      console.info("两个都拿到。。")
                        this.memitem5++;
                       this.span_show[5]="";
                      this.span_show[4]=this.familymembers.concat(this.informationget)[4];

                }









        }







        // 用余数的方法进行统计是否选择，然后赋值给相应的统计总数 
        for(var i = 0;i<this.familymembers.concat(this.informationget).length;i++){
              if(index==i){

                this.span_show[index]=this.familymembers.concat(this.informationget)[index];
  

                //如果之前点击了一次，这次要取消选中
                if(i==0 && (this.memitem0%2==1 ||this.memitem0==1) ){
                 
                      this.memitem0++;
                      // 判断：如果点击双数，则取消选中             
                      this.span_show[0]="";
                       $('.fmember').find(".toplogo .logoimg:eq("+index+")").find("img").animate({"opacity":"0.5"},400,function(){})
         
                }
                else if(i==0 && this.memitem0%2==0){
                      this.memitem0++;
                      this.span_show[0]=this.familymembers.concat(this.informationget)[index];
                         $('.fmember').find(".toplogo .logoimg:eq("+index+")").find("img").animate({"opacity":"1"},400,function(){})
                       // $('.fmember').find(".toplogo:eq("+index+")").css("background","rgb(250,223,118)"); 
                }

                 //如果之前点击了一次，这次要取消选中
                 else if(i==1 && (this.memitem1%2==1 ||this.memitem1==1)){
                      this.memitem1++;
                      // 判断：如果点击双数，则取消选中             
                      this.span_show[1]="";
                       $('.fmember').find(".toplogo .logoimg:eq("+index+")").find("img").animate({"opacity":"0.5"},400,function(){})
      
                }
                else if(i==1 && this.memitem1%2==0){
                      this.memitem1++;
                      this.span_show[1]=this.familymembers.concat(this.informationget)[index];
                       $('.fmember').find(".toplogo .logoimg:eq("+index+")").find("img").animate({"opacity":"1"},400,function(){})
     
                }

                 //如果之前点击了一次，这次要取消选中
                else if(i==2 && (this.memitem2%2==1 ||this.memitem2==1)){
                      this.memitem2++;
                      // 判断：如果点击双数，则取消选中             
                      this.span_show[2]="";
                        $('.fmember').find(".toplogo .logoimg:eq("+index+")").find("img").animate({"opacity":"0.5"},400,function(){})

                }
                else if(i==2 && this.memitem2%2==0){
                      this.memitem2++;
                      this.span_show[2]=this.familymembers.concat(this.informationget)[index];
                       $('.fmember').find(".toplogo .logoimg:eq("+index+")").find("img").animate({"opacity":"1"},400,function(){})
            
                }

                 //如果之前点击了一次，这次要取消选中
                else if(i==3 && (this.memitem3%2==1 ||this.memitem3==1)){
                      this.memitem3++;
                      // 判断：如果点击双数，则取消选中             
                      this.span_show[3]="";
                       $('.fmember').find(".toplogo .logoimg:eq("+index+")").find("img").animate({"opacity":"0.5"},400,function(){})
                }

                else if(i==3 && this.memitem3%2==0){
                      this.memitem3++;
                      this.span_show[3]=this.familymembers.concat(this.informationget)[index];
                       $('.fmember').find(".toplogo .logoimg:eq("+index+")").find("img").animate({"opacity":"1"},400,function(){})
      
                }




                 //如果之前点击了一次，这次要取消选中
                else if(i==4 && (this.memitem4%2==1 ||this.memitem4==1)){
                    console.info("明月2")
                      this.memitem4++;
                      // 判断：如果点击双数，则取消选中             
                      this.span_show[4]="";
                       $('.fmember').find(".toplogo .logoimg:eq("+index+")").find("img").animate({"opacity":"0.5"},400,function(){})
                }
                else if(i==4 && this.memitem4%2==0){
                      this.memitem4++;
                      this.span_show[4]=this.familymembers.concat(this.informationget)[index];
                       $('.fmember').find(".toplogo .logoimg:eq("+index+")").find("img").animate({"opacity":"1"},400,function(){})
                }

                 //如果之前点击了一次，这次要取消选中
                 else if(i==5 && (this.memitem5%2==1 ||this.memitem5==1)){
                      this.memitem5++;
                      // 判断：如果点击双数，则取消选中             
                      this.span_show[5]="";
                       $('.fmember').find(".toplogo .logoimg:eq("+index+")").find("img").animate({"opacity":"0.5"},400,function(){})
                }
                else if(i==5 && this.memitem5%2==0){
                      this.memitem5++;
                      this.span_show[5]=this.familymembers.concat(this.informationget)[index];
                        $('.fmember').find(".toplogo .logoimg:eq("+index+")").find("img").animate({"opacity":"1"},400,function(){})
                }




                // 把家庭成员放到一个排序数组里面
                
              }
            }

            // 发送到父组件this.span_show 
            // 对数组没有选中的，进行处理
            var newarr = [];
            for(var i = 0;i<this.span_show.length;i++){
                if(this.span_show[i]!=""){
                  newarr.push(this.span_show[i])
                }
            };

            // 点击完成后，就发送总的数据事件
            this.$emit('choosedone',newarr);


        },

      mounted:function(){
         
          
        },
        
      addnew:function(){
          // 如果成员小于6个，则可以继续添加发送事件
            if(this.familymembers.concat(this.informationget).length<6){
                this.$emit('getmemberevt',this.familymembers)
            }

          
        },


    components:{
            props:{
              informationget:String
            }
        }

  },
 



}
</script>

<style scoped>

ul,li,p,div,section,header,nav,span,ol,dl,tr,td,th,em,i{
  padding:0;
  margin:0;
}

.box-enter-active,.box-leave-active{
        /*transition: all 1s;*/
    }
.box-enter,.box-leave-active {
        /*transform: translateX(-100%);
        opacity: 0;*/
    }

.itembox{
  width:100%;
  height:auto;

}

.autosay{
  width:100%;
  background:#fff;
  display: inline-block;
  height:auto;
  border-radius: 12px;
  margin-top:10px;
  margin-bottom:5px;
}

.fmember{
  width:300px;
  width:100%;
  height:auto;
  overflow: hidden;
  font-size: 12px;

}

.toplogo{
  width:23%;
  height:80px;
  float:left;
  position: relative;
}
.toplogo span{
  width:100%;
  height:100%;
  opacity: 0.4;
  position: absolute;
  left:0;
  top:0;
  z-index: 88;
}

*{-webkit-tap-highlight-color: rgba(0,0,0,0);-webkit-tap-highlight-color:transparent;};

.logoimg{
  width:100%;
  height:50px;
}

.logoimg img{
  width:50px;
  height:50px;
  display: block;
  margin:0 auto;

  opacity: 0.5;
}

.logoimg .littleson,.logoimg .littlegirl{
  opacity: 1;
}


.addincon.logoimg img{
   opacity: 1;
}


.logotext{
  width:100%;
  height:20px;
  text-align: center;
  line-height: 20px;
  font-size: 12px;
}





</style>

























