.mw_box{
  width:85%;
  height:auto;
  background: #fff;
  border-radius: 12px;
  margin:0 auto;
  margin-top:40%;
}

.mw_box h2{
  height:80px;
  line-height: 80px;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
}

.mw_spanbox{
  width:100%;
  height:150px;
  display: inline-block;

}

.mw_spanbox span{
  width:50%;
  height:100%;
  float:left;
  padding-top:20px;
  box-sizing:border-box;
}
.mw_spanbox i{
  display: block;
  width:70px;
  height:70px;
  margin:0 auto;
  font-style: normal;
  background-image: url("../static/img/son.png");
  background-size: 100% 100%;

  opacity: 0.5;
}
.mw_spanbox p{
  text-align: center;
  font-style: 12px;
  padding-top:12px;
}
.mw_spanbox .ibg{
    background-image: url("../static/img/girls.png");
}

.closewindow{
  width:100%;
  height:40px;
  display: block;
  text-align: center;
  font-size: 30px;
  font-weight: 600;


}
.closewindow span{
  width:25px;
  height:25px;
  display: inline-block;
  background-image:url("../static/img/close.png");
  background-size:100% 100%;
}

.mint-datetime-action{
  color:#fff;
}

