

<template>
  <div class="loginandout">

        <!-- 说明：slideInLeft之类动画起作用。不过如果styel里定义了样式，那么就以style动画样式为主 -->
 <!--    <transition name="box" enter-active-class="animated slideInLeft" leave-active-class="animated rotateOut">
      <div v-show='showlogins'>
       		<p>12333333333333</p>
      </div>
    </transition> -->


    <transition name="box" enter-active-class="animated fadeInRight" leave-active-class="animated rotateOut">

      <div v-show='showlogins' class="itembox">
          <div class='autosay'>
               <!-- 给你点个赞。。 -->
               <slot></slot>

          </div>

      </div>
    </transition>



  </div>
</template>

<script>
export default {
  data(){
    return{
      // data:
    }
  },
  props : {
        showlogins : {
            type : Boolean,
            required : true
        }
    },
  methods:{
    // closemoreprojects:function(){
    //   this.$emit('closeprojects');
    // },
  }

}
</script>

<style scoped>

ul,li,p,div,section,header,nav,span,ol,dl,tr,td,th,em,i{
  padding:0;
  margin:0;
}

.box-enter-active,.box-leave-active{
        /*transition: all 1s;*/
    }
.box-enter,.box-leave-active {
        /*transform: translateX(-100%);
        opacity: 0;*/
    }

.itembox{
  width:100%;
  height:auto;
  overflow: hidden;
  /*background: #fff;*/
}



.autosay{
  width:auto;
  min-width: 35px;
  max-width: 85%;
   height:auto;
  background:#fff;
  margin-right:18px;
  float:right;
  display: inline-block;
  border-radius: 12px;
  margin-top:10px;
  margin-bottom:10px;
  padding:8px;
  padding-left:14px;
  padding-right:14px;
  color:rgb(65,65,65);
  line-height: 150%;

}


.autosay div{
  margin-top:3px;
  margin-bottom:2px;
}















</style>

























