

<template>
  <div class="loginandout">



    <transition name="box">

      <div v-show='showlogins' class="itembox">
        <div class='autosay' >
              

              
            <div class="div1">
                  <label @click="clickit1" >

                        <input style="display:none;" class="input1" name="Fruit" type="radio" value="" /> 


                        <div class="imgs">
                           <img src="../../../static/img/dady.png" alt="">
                        </div>
                        <div class='text'>
                            先生
                        </div>
                  </label> 

            </div>

               <div class="div2"  >
                  <label @click="clickit2">

                          <input style="display:none;" class='input2' name="Fruit" type="radio" value="" /> 

                        <div class="imgs">
                           <img  src="../../../static/img/mami.png" alt="">
                        </div>
                        <div class='text'>
                            女士
                        </div>
                  </label> 

            </div>  
        
          </div>
      </div>
    </transition>



  </div>
</template>

<script>
export default {
  data(){
    return{
      // data:
      chooserole:""
    }
  },
  props : {
        showlogins : {
            type : Boolean,
            required : true
        }
    },
  methods:{
    // closemoreprojects:function(){
    //   this.$emit('closeprojects');
    // },
    clickit:function(e){
          
    },

    clickit1:function(){
          if($(".input1").prop("checked")){
             $(".div1").animate({"opacity":"1"},400,function(){})
             $(".div2").animate({"opacity":"0.5"},400,function(){})
             // 发送chooserole信息，先设置
             this.chooserole ="先生";
             this.$emit('chooseroleevt',this.chooserole);
        }

    },

     clickit2:function(){
          if($(".input2").prop("checked")){
             $(".div2").animate({"opacity":"1"},400,function(){})
             $(".div1").animate({"opacity":"0.5"},400,function(){})
             // 发送chooserole信息，先设置
             this.chooserole ="女士";
             this.$emit('chooseroleevt',this.chooserole);
        }
       
    },








  },










  mounted:function(){
      // console.info("获取表单事件》。");
      // $("input").attr("checked","checked"); //设置单选事件
      // console.info("000"+$("input").prop("checked"));//获取单选事件

  }

}
</script>

<style scoped>

ul,li,p,div,section,header,nav,span,ol,dl,tr,td,th,em,i,input,label{
  padding:0;
  margin:0;
}

.box-enter-active,.box-leave-active{
        /*transition: all 1s;*/
    }
.box-enter,.box-leave-active {
        /*transform: translateX(-100%);
        opacity: 0;*/
    }

.itembox{
  width:100%;
  height:auto;
}


*{-webkit-tap-highlight-color: rgba(0,0,0,0);-webkit-tap-highlight-color:transparent;};
  
.autosay{
  width:300px;
  height:100px;
  padding-top:20px;
}
.div1,.div2{
  width:45%;
  height:90%;
  height:auto;
  float:left;
  padding-top:12px;

  opacity: 0.5;

}

.imgs{
  width:40%;
  height:40%;
  margin:0 auto;
}

.text{
  width:100%;
  font-size: 14px;
  text-align: center;
  margin-top:12px;
}
.imgs img{
  width:100%;
  height:100%;
}












</style>

























